@import url('https://fonts.googleapis.com/css2?family=Style+Script&display=swap');
.navbar{
    display: flex;

    height: 55px;
    align-items: center;
    border-bottom: 0.2px solid rgba(0, 0, 0, 0.172);
    padding: 10px;
    box-sizing: border-box;
    justify-content: space-between;
    width: 100%;
    .title{
        font-weight: 900;
        font-size: 28px;
        font-family: 'Style Script', cursive;
    }
    #search{
        width: 300px;
        height: 25px;
        background-color: rgba(231, 229, 229, 0.269);
        border-radius: 5px;
        border: 0.2px solid rgba(0, 0, 0, 0.172);
    }
    ::-webkit-input-placeholder{
        text-align: center;
    }
    .sub-icon{
        height: 20px;
        margin: 0 7px;
    }
    .btn{
        margin: 0 5px;
        button{
            height: 25px;
            width: 70px;
            font-weight: 700;
            cursor: pointer;
        }
    }
    .login{
        background-color: rgba($color: #0073ff, $alpha: 1.0);
        color: white;
        border: none;
        border-radius: 3px;
    }
    .signin{
        background-color: white;
        border: none;
        color: #0073ff;
    }
}

@media only screen and (max-width: 769px) {
    .navbar{
        #search{
            width: 200px;
        }
    }
}

@media only screen and (max-width: 541px) {
    .navbar{
        #search{
            display: none;
        }
        .navicon{
            display: none;
        }
        .title{
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
        }
    }
}

