@import '../../globalData';
.footer{
    height: 80px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 14px;
    color: rgba(71, 71, 71, 0.71);
    background-color: $primary;
    a{
        margin: 0 5px;
        text-decoration: none;
        font-size: 14px;
        color: rgba(71, 71, 71, 0.71);
    }
    .footer-content-1{
        margin-bottom: 5px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

}